import * as React from 'react';
import compose from 'recompose/compose';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { Query } from 'react-apollo';
import Titlebar from '../Titlebar';
import { View } from './style';
import { InboxScroller, CommunityListItem } from 'Components/Dashboard/style';
import SearchInput from './searchInput';
import { Loading } from 'Components/Loading';
import { cookies } from 'link';
import { User } from 'CustomTypings/schema';
import { CURRENT_USER } from '../../constants';
import { SEARCH_USER } from 'Graphql/Query';
import ListItem from 'anchor-ui/list-item';
import { withCurrentUser } from 'Utils/withCurrentUser';

// tslint:disable-next-line:no-empty-interface
interface Props {
  currentUser?: any;
}
interface State {
  searchQueryString?: string;
}

class Search extends React.Component<Props, State> {
  state = { searchQueryString: '' };

  handleSubmit = (searchQueryString: string) => {
    if (searchQueryString.length > 0) {
      this.setState({ searchQueryString });
    }
  }

  render() {
    const { searchQueryString } = this.state;
    const { currentUser } = this.props;
    const searchFilter = { everythingFeed: true };

    return (
      <View>
        <SearchInput handleSubmit={this.handleSubmit} />
        <Query
          pollInterval={100000}
          query={SEARCH_USER}
          variables={{ text: searchQueryString, username: currentUser.username ? currentUser.username : ''}} >
        {({loading, error, data}) => {
          if (loading) { return <Loading />; }
          if (error) { return <div>Error loading</div>; }

          return (
            <InboxScroller id="scroller-for-inbox" style={{ zIndex: 5}}>
              {searchQueryString &&
                data.searchUser.length > 0 
                && 
                data.searchUser.map((user) => (
                  <Link to={`/n/${user.username ? user.username : ''}`}>
                    <ListItem
                      key={user.id}
                      primaryText={user.firstname + ' ' + user.lastname}
                      secondaryText={user.username ? user.username : ''}
                      avatar={user.avatar}
                    />
                  </Link>
                ))}
            </InboxScroller>
          );
        }}
        </Query>

      </View>
    );
  }
}

export default withCurrentUser(Search);
